.bg_animation {
  background-color: #1c1c1c;
}

.title_left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 40%;
  /* height: 70%; */
  margin: 10px;
}

.title_right {
  width: 40%;
  /* height: 70%; */
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: #412eb5; */
}

.title {
  color: white;
  font-size: 6vh;
  margin-bottom: 4vh;
}

.btn_primary {
  font-size: 1.4vh; 
  background-color: #1c41e7;
  border: 2px solid #1c41e7;
  border-radius: 10px;
  margin-right: 1vw;
  padding: 5px 10px 5px 10px;
  min-width: fit-content;
  min-height: fit-content;
}

.btn_secondary {
  font-size: 1.4vh;
  border: 2px solid #1c41e7;
  border-radius: 10px;
  margin-right: 1vw;
  padding: 5px 10px 5px 10px;
  min-width: fit-content;
  min-height: fit-content;
}
